@font-face {
  font-family: "KOHIBaeumOTF";
  src: url("https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2201-2@1.0/KOHIBaeumOTF.woff")
    format("woff");
  font-weight: normal;
  font-style: normal;
}

.mypage {
  font-family: "Pretendard-Regular";
  background-color: #f5f5f5;

  .mypageBox {
    background-color: white;
    padding: 30px 40px;
    margin-bottom: 10px;
    border-radius: 15px;

    .mypageTop {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 12px 0;
      border-bottom: 1px solid #f5f5f5;

      .mypageTopName {
        font-weight: 800;
        font-size: 24px;
        color: #8785a2;
        font-family: "KOHIBaeumOTF";
      }
      .mypageTopIcon {
        width: 22px;
        height: 22px;
      }
    }
    .mypageProfileBox {
      padding: 24px 0;
      display: flex;
      flex-direction: row;
      justify-content: space-between;

      .mypageProfileText {
        display: flex;
        flex-direction: column;
        justify-content: space-between;

        .mypageNickname {
          font-size: 24px;
          font-weight: 800;
          color: black;
        }
        .mypageSideInfo {
          color: #454545;
          font-weight: 500;
          font-size: 14px;
        }
        .mypageLogoutBtn {
          color: #8785a2;
          font-weight: 500;
          font-size: 14px;
        }
        .mypageLogoutBtn:hover {
          cursor: pointer;
        }
      }
    }
    .mypageTitle {
      display: flex;
      justify-content: center;
      align-items: center;
      padding-bottom: 10px;

      .mypageTitleText {
        margin-right: 10px;
        font-size: 18px;
        font-weight: 800;
      }
      .mypageTitleIcon {
        width: 18px;
        height: 18px;
      }
    }
  }
}

.bottomback {
  height: 70px;
  background-color: aqua;
}

// Images
.mypage-profile-img-wrapper {
  width: 90px;
  height: 90px;
  overflow: hidden;
  border-radius: 50%;
}
.mypage-profile-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.mypageIcons {
  display: flex;
  justify-content: space-around;
  .mypageIcon {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  p {
    text-align: center;
    font-family: "Pretendard-Regular";
    padding-top: 5px;
  }
}
